body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: bg-light;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #f8f9fa;
  display: block;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(6px + 2vmin);
  color: black;
}

.App-menu {
  background-color: #f8f9fa;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  margin-top: 0 !important;
}

.App-link {
  color: #61dafb;
}

.my-title {
  font-size: 1.6rem;
}

.card:hover{
  background-color: rgba(108, 117, 125, 0.1);
	border-radius: 25px;	
}

.my-transparentDark{
  background-color: rgba(108, 117, 125, 0.93);
}

.my-flexWrap{
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  display: -webkit-flex;
  display: flex;
}

h2.sidelines { text-align: center; border-bottom: 1px solid #b4b8b8; height: 0.75em; }
h2.sidelines span { display: inline-block; background: #f8f9fa; padding:0 0.5em; }

h2.sidelinesdark { text-align: center; border-bottom: 1px solid rgb(211, 208, 208); height: 0.75em; }
h2.sidelinesdark span { display: inline-block; background: #6c757d; padding:0 0.5em; }


@media screen and (max-width: 991.98px) and (min-width:768px){
  .my-title{
    font-size: 3.2rem;
  }
}

@media screen and (min-width:992px){
  .my-title{
    font-size: 4.8rem;
  }
}

.my-toolbar {  
  position: fixed;
  
  top: 1rem;
  right: 1rem;
  z-index: 2;  
}



.my-navbar {  
  
  position: fixed;
  top: 0rem;
  z-index: 1;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (min-width: 992px){
  .App-header{
    margin-top: 1rem;
  }
}

.fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }
